<template>
	<div class="box p-0">
		<div class="hero is-medium" v-if="!fetchDone">
			<div class="hero-body has-text-centered has-text-white">
				{{$t('donations.loading')}}
			</div>
		</div>
		<section class="section" v-else-if="!authenticated && fetchDone">
			<div class="container">
					<div class="content has-text-centered">
					<p>{{$t('donations.loginMessage')}}</p>
					</div>
					<div class="buttons is-centered">
					<a :href="makeUrl('/signin','/convencion/ong')" class="button is-medium is-warning">{{$t('donations.loginButton')}}</a>
					</div>
			</div>
		</section>
		<section class="section payday-container" v-else-if="authenticated && fetchDone">
			<div v-if="showStep == 1">
				<h1 class="subtitle is-5 has-text-centered">{{$t('donations.step')}} 1</h1>
				<h1 class="title is-3 has-text-centered">{{$t('donations.stepOneTitle')}}</h1>
				<div class="columns is-centered mt-5">
					<div class="column is-narrow">
						<a @click="changePaymentType('MP')"><img src="https://new.furfiesta.com/wp-content/uploads/2021/10/697ab8773d917d5efba30cc803ea1ce66d74e227.png" class="image mx-auto" width="200" alt=""></a>
					</div>
					<div class="column is-narrow is-offset-1">
						<a @click="changePaymentType('PP')"><img src="https://new.furfiesta.com/wp-content/uploads/2021/10/740c1425f49b39643947f1f4964ff75d5bd885e8.png" class="image mx-auto" width="200" alt=""></a>
					</div>
				</div>
			</div>
			<div v-if="showStep == 2">
				<a href="#" @click="goBack1" class="has-text-info goback">⬅️ {{$t('donations.changeMethodButton')}}</a>
				<h1 class="subtitle is-5 has-text-centered">{{$t('donations.step')}} 2</h1>
				<h1 class="title is-3 has-text-centered">{{$t('donations.stepTwoTitle')}}</h1>
				<div class="my-6">
				<b-field>
					<b-slider v-model="payday" size="is-large" type="is-info" :step="stepSlider" indicator :bigger-slider-focus="false" :tooltip="false" :min="minSlider" :max="maxSlider"></b-slider>
        </b-field>
				</div>
				<h1 class="subtitle is-4 has-text-centered">{{$t('donations.totalAmount')}} <span class="has-text-weight-bold has-text-info">{{payday}}&nbsp;{{paymentType == 'MP' ? 'AR$' : 'USD'}}</span></h1>
				<div class="buttons is-centered">
					<button @click="goStep3" class="button is-medium is-primary">{{$t('donations.continue')}}</button>
				</div>
			</div>
			<div v-if="showStep == 3">
				<a href="#" @click="goBack2" class="has-text-info goback">⬅️ {{$t('donations.changeAmountButton')}}</a>
				<h1 class="subtitle is-5 has-text-centered">{{$t('donations.step')}} 3</h1>
				<h1 class="title is-3 has-text-centered">{{$t('donations.stepThreeTitle')}}</h1>
				<div class="my-6">
					<h5 class="title is-5 mb-3">{{$t('donations.titleDonationOng')}}</h5>
					<div class="columns is-vcentered">
						<div class="column">
							<div class="pr-3">

				<b-field>
					<b-slider v-model="cutOng" size="is-large" type="is-info" :step="1" :tooltip="false" format="percent" :min="0" :max="100" indicator @input="(value) => changeCut('arff',value)" ></b-slider>
        </b-field>
							</div>
						</div>
						<div class="column is-2">
							<div class="has-text-right is-size-5 has-text-centered-touch">
							<b>{{amountOng}}&nbsp;{{currencySymbol}}</b>
							</div>
						</div>
					</div>
					<h5 class="title is-5 mb-3">{{$t('donations.titleDonationArff')}}</h5>
					<div class="columns is-vcentered">
						<div class="column">
							<div class="pr-3">

				<b-field>
					<b-slider v-model="cutArff" size="is-large" type="is-info" :step="1" :tooltip="false" format="percent" :min="0" :max="100" indicator @input="(value) => changeCut('ong',value)"></b-slider>
        </b-field>
							</div>
						</div>
						<div class="column is-2">
							<div class="has-text-right is-size-5 has-text-centered-touch">
							<b>{{amountArff}}&nbsp;{{currencySymbol}}</b>
							</div>
						</div>
					</div>
				</div>
				<hr class="has-background-primary">
				<div class="columns is-vcentered mb-0">
						<div class="column has-text-right has-text-centered-touch">
							<span class="is-size-5-desktop is-size-6 ">{{$t('donations.totalAmount')}}</span>
						</div>
						<div class="column is-2">
							<div class="has-text-right has-text-info is-size-5 has-text-centered-touch">
							<b> {{amountPayday}}&nbsp;{{currencySymbol}}</b>
							</div>
						</div>
					</div>
				<hr class="has-background-primary mt-3">
				<br>
				<h1 class="title is-5 has-text-centered">{{userNames}},</h1>
				<h1 class="subtitle is-5 has-text-centered" v-if="amountOng > 0 && amountArff > 0">{{$t('donations.finalQuantityBoth', {ong: amountOng, arff: amountArff, currency: currencySymbol})}} </h1>
				<h1 class="subtitle is-5 has-text-centered" v-else-if="amountOng > 0 && amountArff == 0">{{$t('donations.finalQuantityOng', {ong: amountOng, currency: currencySymbol})}} </h1>
				<h1 class="subtitle is-5 has-text-centered" v-else-if="amountOng == 0 && amountArff > 0">{{$t('donations.finalQuantityArff', {arff: amountArff, currency: currencySymbol})}} </h1>
				<br>
				<h1 class="title is-4 has-text-centered">{{$t('donations.readyToDonate')}}</h1>
				<div class="buttons is-centered">
					<button @click="startPayment" class="button is-large is-info">{{$t('donations.goTo',{method: paymentType == 'MP' ? 'Mercado Pago' : 'PayPal'})}}</button>
				</div>
			</div>
		</section>
	</div>
</template>

<script>
import userUtils from '../mixins/userUtils'
export default {
	data(){
		return {
			fetchDone: false,
			authenticated: false,
			paymentType: null,
			doneSelecting: null,
			showStep: 1,
			payday: 0,
			cutArff: 50,
			cutOng: 50
		}
	},
	mixins: [userUtils],
	mounted: function () {
		// REQUIRED
		this.fetchDone = false
		this.$http.get('/user')
		.then(response => {
			this.$store.commit('updateUser', response.data)
			this.authenticated = true
		})
		.catch(() => {})
		.finally(() => {
			this.fetchDone = true
			// console.error(errUser)
		})
	},
	computed: {
		amountPayday: function(){
			return this.payday.toFixed(2)
		},
		amountArff: function(){
			return (this.payday * (this.cutArff/100)).toFixed(2)
		},
		amountOng: function(){
			return (this.payday * (this.cutOng/100)).toFixed(2)
		},
		currencySymbol: function(){
			if(!this.paymentType) return '??$'
			return this.paymentType == 'MP' ? 'AR$' : 'USD'
		},
		minSlider: function(){
			if(!this.paymentType) return 100
			if(this.paymentType == 'MP') return 100
			if(this.paymentType == 'PP') return 5
			return 100
		},
		maxSlider: function(){
			if(!this.paymentType) return 75000
			if(this.paymentType == 'MP') return 75000
			if(this.paymentType == 'PP') return 500
			return 10000
		},
		stepSlider: function(){
			if(!this.paymentType) return 100
			if(this.paymentType == 'MP') return 100
			if(this.paymentType == 'PP') return 5
			return 100
		}
	},
	methods: {
		goStep3() {
			this.cutArff = this.cutOng = 50
			this.showStep = 3
		},
		goBack2(){
			this.showStep = 2
		},
		goBack1(){
			this.payday = 0
			this.paymentType = null
			this.showStep = 1
		},
		changeCut: function(who, val){
			if(who == 'arff'){
				this.cutArff = 100-val
			} else if(who == 'ong') {
				this.cutOng = 100-val
			}
		},
		changePaymentType: function(type){
			this.showStep = 2
			this.paymentType = type
			this.payday = 0
		},
		startPayment: async function () {
			if(this.paymentType == 'MP') {
				this.checkoutMercadoPago()
			} else {
					await this.$swal.fire({
						title: this.isSpanish ? 'ESTO ES MUY IMPORTANTE' : 'THIS IS VERY IMPORTANT',
						icon: 'warning',
						text: this.isSpanish ? 'Cuando finalices tu pago, es importante que hagas clic en el botón "VOLVER AL VENDEDOR" para que tu pago se acredite instantaneamente' : 'After you\'ve done the checkout, its important that you click the button "GO TO MERCHANT" so your payment is instantaneously accredited',
						showCancelButton: true,
						cancelButtonText: this.isSpanish ? 'Cancelar' : 'Cancel',
						allowOutsideClick: false,
						allowEscapeKey: false,
						allowEnterKey: false,
						confirmButtonText: this.isSpanish ? '¡OK! ¡Entendido!' : 'OK! I will do it!',
					didOpen: () => {
						this.$swal.showLoading()
					setTimeout(() => {
						this.$swal.hideLoading()
						},5000)
					}
				}).then((res) => {
					if (res.isConfirmed){
						this.checkoutPaypal()
					}
					if (res.isDismissed) {
						this.closeModalPayment()
					}
				})
			}
		},
		checkoutMercadoPago: function () {
			this.$swal.fire({
				title: this.isSpanish ? 'Redirigiendote a Mercado Pago' : 'Redirecting you to Mercado Pago',
				text: this.isSpanish ? 'Tu navegador te llevará automaticamente  a Mercado Pago' : 'Your browser will automatically take you to Mercado Pago',
				icon: 'info',
				timer: 20000,
				timerProgressBar: true,
				showConfirmButton: false,
				didOpen: () => {
					this.$http.post("https://api.furfiesta.com/charity/checkout",{
						currency: 'ARS',
						amount: this.payday,
						percentage: this.cutArff,
						//test: 'mapachitos'
					}).then((response) => {
						location.href = 'https://api.furfiesta.com' + response.data.redirectURL
					}).catch( err => {
						console.error(err)
					})
				},
			}).then((result) => {
				/* Read more about handling dismissals below */
				if (result.dismiss === this.$swal.DismissReason.timer) {
					console.log('I was closed by the timer')
				}
				this.closeModalPayment()
				return
			})
		},
		checkoutPaypal: function() {
		if(this.readOnly) return
			this.$swal.fire({
				title: this.isSpanish ? 'Redirigiendote a PayPal' : 'Redirecting you to PayPal',
				text: this.isSpanish ? 'Tu navegador te llevará automaticamente a PayPal' : 'Your browser will automatically take you to PayPal',
				icon: 'info',
				timer: 20000,
				timerProgressBar: true,
				showConfirmButton: false,
				didOpen: () => {
					this.$http.post("https://api.furfiesta.com/charity/checkout",{
						currency: 'USD',
						amount: this.payday,
						percentage: this.cutArff,
						//test: 'mapachitos'
					}).then((response) => {
						location.href = 'https://api.furfiesta.com' + response.data.redirectURL
					}).catch( err => {
						console.error(err)
					})
				},
			}).then((result) => {
				/* Read more about handling dismissals below */
				if (result.dismiss === this.$swal.DismissReason.timer) {
					console.log('I was closed by the timer')
				}
				this.closeModalPayment()
				return
			})
		},
	}
}
</script>

<style lang="scss" scoped>
.goback{
	position: absolute;
	top: 15px;
	left: 15px;
}
.payday-container{
	position:relative;
}
</style>
